* {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img {
  border: none;
}

address, caption, cite, code, dfn, em, strong, th {
  font-style: normal;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 200%;
  font-weight: normal;
}

:focus {
  outline: none;
}

a {
  text-decoration: none;
}

html, body {
  width: 100vw;
  height: 100vh;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  transform: translateZ(0);
}

body {
  overflow-y: overlay;
}

.scroll {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  display: grid;
}

.result-item-fullbox {
  box-sizing: border-box;
  cursor: pointer;
  margin: 4px;
  transition: all .3s;
}

.result-item:hover {
  background-color: #f5f8fa;
}

.result-item {
  width: 98%;
  height: 130px;
  max-width: 400px;
  box-sizing: border-box;
  border-radius: 5px;
  flex: 1;
  justify-content: space-between;
  margin: 0 auto;
  padding: 8px 16px;
  transition: all .3s;
  display: flex;
  position: relative;
  box-shadow: 0 2px 12px rgba(0, 0, 0, .1);
}

.result-item-img {
  height: 90px;
  width: 90px;
}

.result-item-img img {
  width: 90px;
  height: 90px;
  opacity: 0;
  border-radius: 5px;
  transition: opacity 1s;
}

.result-item-content {
  width: 70%;
  color: #333;
}

.result-item-content-title {
  color: #36517a;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 2px;
  font-size: 18px;
  font-weight: 700;
  overflow: hidden;
}

.result-item-content-text {
  text-overflow: ellipsis;
  box-sizing: border-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  padding-right: 8px;
  font-size: 16px;
  display: -webkit-box;
  overflow: hidden;
}

.result-item-date {
  color: #999;
  font-size: 14px;
  position: absolute;
  bottom: 12px;
}

.next-page {
  width: 100%;
  height: 50px;
  cursor: pointer;
  color: #93969a;
  background: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
}

.next-page-text {
  display: none;
}

.spinner {
  width: 40px;
  height: 40px;
  animation: rotate 2s linear infinite;
  display: block;
}

.spinner .path {
  stroke: #93bfec;
  stroke-linecap: round;
  animation: dash 1.5s ease-in-out infinite;
}

.saerch-full {
  min-height: 50px;
  display: flex;
}

.saerch-full .search-input {
  width: calc(100% - 50px);
  box-sizing: border-box;
}

.saerch-full .search-button {
  width: 50px;
  min-height: 50px;
  font-size: 18px;
  display: block;
}

.saerch-full .search-button button {
  width: 100%;
  height: 100%;
  font-size: 18px;
  display: block;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1 150;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 90 150;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 90 150;
    stroke-dashoffset: -124px;
  }
}

.skeleton {
  background-image: linear-gradient(90deg, #efecee 25%, #fff 37%, #efecee 63%);
  background-position: 100%;
  background-size: 400% 100%;
  animation: skeleton-loading 1.4s infinite;
}

@keyframes skeleton-loading {
  0% {
    background-position: 100%;
  }

  100% {
    background-position: 0;
  }
}

